    &__content-container {
      height: 555px;
      padding-top: 92px; } }

  .input-box {
    &__button {
      width: 200px; } }

  .select {
    width: 200px;
    height: 70px;
    border: 0.5px solid rgba(25, 188, 228, 0.3);
    padding: 10px 30px;
    text-align: left; }

  .second-section {
    padding-top: 60px;
    padding-bottom: 60px;
    &__left-block {
      width: 440px;
