  .people-card {

    width: 308px;
    height: 280px;
    background: #FFFFFF;
    &__img {
      left: 82px; } }

  .subscription-block {
    padding-top: 24px;
    padding-bottom: 24px; }

  .footer-section {
    padding-top: 65px;
    padding-bottom: 30px;
    &__subscription-block {
      position: absolute;
      bottom: 270px; } } }

